.container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 25px 85px 65px 85px;
    /* width: 607px; */
    /* height: 829px; */
    background: rgba(255, 255, 255, 0.08);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(2.5px);
    -webkit-backdrop-filter: blur(2.5px);
    border: 1px solid rgba(255, 255, 255, 0.31);
    user-select: none;
}

.topbar {
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 60px;
}

.topbar input {
    display: flex;
    width: 362px;
    height: 78px;
    background-color: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
}

.searchIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 78px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}

.weatherImage {
    margin-top: 29px;
    display: flex;
    justify-content: center;
}

.weatherTemp {
    display: flex;
    justify-content: center;
    color: #fff;
    font-size: 120px;
    font-weight: 400;
}

.weatherLocation {
    display: flex;
    justify-content: center;
    color: #fff;
    font-size: 60px;
    font-weight: 400;
}

.weatherName{
    font-size: 18px;
    font-weight: 300;
}

.dataContainer {
    margin-top: 50px;
    color: #fff;
    display: flex;
    justify-content: center;
}

.element {
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
}

.data {
    font-size: 34px;
    font-weight: 400;
}

.text {
    font-size: 20px;
    font-weight: 400;
}

.icon {
    margin-top: 10px;
}